button {
  height: 100%;
  width: 100%;
  background-color: transparent;
  font-size: 2em;
  padding: 0;
  border-style: none;
  border: 1px solid black;
}

.paren {
  -ms-flex: 1 1;
      flex: 1 1;
  background-color: #d6d6d6;
}

.clear {
  -ms-flex: 2 1;
      flex: 2 1;
  background-color: #d6d6d6;
}

.num-button {
  -ms-flex: 1 1;
      flex: 1 1;
  background-color: #f5f5f5;
}

.operator-button {
  -ms-flex: 1 1;
      flex: 1 1;
  background-color: #d6d6d6;
}

.period-button {
  -ms-flex: 1 1;
      flex: 1 1;
  background-color: pink;
}

.equals-button {
  -ms-flex: 1 1;
      flex: 1 1;
  background-image: -webkit-linear-gradient(top,#4d90fe,#4787ed);
  color: white;
}

.equals-button button {
  color: white;
}


